:root {
  --seafoam: #6D8C87;
  --gray: #404040;
  --magenta: #8F0458;
  --sage: #ACB3AB;
}

body {
  background-color: offwhite;
  margin: 0;
}

.nav{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 15vh;
  background: rgb(218, 226, 230);
  color: white;
  margin: 0;
  font-size: 25px;
  width: 100%;  
  border: solid 2px white;
}

.nav-links{
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  font: 1em sans-serif; 
}

.nav-links a{
  text-decoration: none;
  font-family: 'Open Sans', sans-serif;
  color: white;
}

#ka{
  border-radius: 50%;
  border: 4px solid white;
  height: 70px;
  width: 70px;
  align-items: center;
}

.home-container{
  background-image: url('https://miro.medium.com/max/12000/1*pUi3vkj06Vqp_sXeiI-UbQ.jpeg');
  height: 100vh;
  width: 100vw;
  position: relative;
  background-size: cover;
  background-position: 50% 50%;  
}

.home{
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: rgba(0,0,0,0.5);
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;  
}

.home h1 {
  font-size: 65px;
  letter-spacing: 2px;
  margin: 0 0 5px;
}

.home p{
  margin: 0;
  font-size: 30px;
}
/* ABOUT_ME */
.about-container{
    display: flex;
    flex-direction: row;
    background-color: gainsboro;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 100%;   
}

.about { 
  display: flex;
  box-sizing: border-box;
  padding: 40px 5px 30px 5px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
  width: 500px; 
}

#meDivider{
  width: 80%;
  margin: 0 auto;
  max-width: 1300px;
  color: white;
}

.aboutMe{
  background-color: gainsboro;
  font-size: 2.5em;
  text-shadow: 2px 2px 1px ghostwhite;
}
#h1Me{
  margin-top: 0;
}

.about{
  align-items: center;
  margin: 0;
} 

.hireMe{
  background-color: gainsboro;
}

.hireMe h1{
  text-align: center;
  font-size: 3.5em;
  text-shadow: 2px 2px 1px ghostwhite;
  margin: 0;
  padding: 30px;
}

.pDivider p{
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 3% auto 8%;
  text-align: center;
  margin: 0;
  padding:  50px;

}

#resume-button{
  border: 3px solid ghostwhite;
  background: rgb(218, 226, 230);
  border-radius: 5px;
  color: white;
  font-size: 20px; 
}

#resume-button:hover{
  background-color: offwhite;
  color: black;
}

h3{
  margin: 0;
}

h1, h2, h3, h4, p{
  font-family: "Shippori mincho";

}

.projects-container h1{
  font-size: 3.5em;
  text-shadow: 2px 2px 1px ghostwhite;
}

#portfolio-picture{
  border-radius: 50%;
  border: 4px solid var(--sage);
  height: 400px;
  width: 400px;
  padding: 0;
}

#portfolio-picture:hover{
  border: 4px solid var(--magenta);

}


#about-me{
  text-align: center;
  color: white;
}

#pb{
  display: flex;
  background-color: var(--seafoam);
  color: white;
  width: 100%;
}


#pb p{
  margin: 0 auto;
  width: 500px;
}

.skillColor{
  background-color: #ACB3AB;
}

#skillH1{
  margin: 0;
  padding: 50px;
  font-size: 2.5em;
  text-shadow: 2px 2px 1px ghostwhite;
}

#skillDivider{
  width: 80%;
  margin: 0 auto;
  max-width: 1300px;
  color: white;
  
}
/* SKILLS */
.skills{
  display: flex;
  justify-content: space-around;
  align-items: center;
  min-height: 10vh;
  /* display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  margin-top: 30px; */
  padding: 50px;
  background-color: #ACB3AB;
  margin: 0;
  box-sizing: border-box; 
}

.skills-icons{
  display: flex;
  width: 80%;
  margin: 0 auto;
  flex-wrap: wrap; 
}

.skills-icons li{
  display: flex;
  list-style: none;
  text-align: center;
  width: 25%;
  padding: 10px;
  font-size: 20px;
  color: whitesmoke;
  flex-direction: column; 
}

#size {
  font-size: 50px;
}


/* .social-link{
  width: 40%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  list-style: none;
  font: 1em sans-serif;
} */



/* PROJECTS */
.projects-container{
  background-color: #f4f4f4;
  align-items: center;
  margin: 0;
}
h1,p{
  text-align: center;
}

#projectH1{
  margin: 0;
  margin-bottom: 30px;
  font-size: 3.5em;
  text-shadow: 2px 2px 1px ghostwhite;
}
#project-divider{
    width: 80%;
    margin: 0 auto;
    max-width: 1300px;
    color: white;
  
}

.projectTitle{
  margin-bottom: 5%;
}

.projects-ul{
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 0;
}

.projects-ul li{
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 10px 0px;
  width: 80%;
}


.projects-ul img{
  display: flex;
  flex-direction: row;
  width: 50vmin;
  height: 40vmin;
  min-width: 350px;
  min-height: 250px;
  margin: 20px;
  object-fit: cover;
}
img{
  border: 5px solid white;
}

img:hover{
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);

}

.icon-ul{
  display: flex; 
  width: 100px;
  font-size: 30px;
  justify-content: space-between;
  
  
  
}

.paragraph{
  color: #6D8C87;
}

/* need help */
.lag{
  display: flex; 
  width: 60%;
  justify-content: space-between;
  color: #6D8C87;
  
  
}

h4{
  color: #6D8C87;
}

.fab{
  display: flex;
  justify-content: space-between;
  flex-direction: column;
}
/* CONTACTS */
.footer{
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  color: white;
  background:rgb(145, 152, 155);
  margin: 0;
  
}

li{
  list-style-type: none;
  display: flex;
  align-items: center;
  min-height: 2vh;
  flex-direction: row;
}

.footer-ul{
  display: flex; 
  justify-content: center;
  width: 80px;
  font-size: 30px;
  justify-content: space-between;
  

}

.fab{
  color:#8F0458;
  
 
}



.fas{
  color:#8F0458;
}

li a {
  text-decoration: none;
}

